<template>
    <div class="box">
        <div class="mask clearfix" :class="{'active':isShow}" v-if="!isShow">
            <div class="rack-top clearfix" >
                <div class="fl top-title" @click="allcheck" >{{allCheck?'全选':'取消全选'}}</div>
                <div class="fr top-title" @click='isShow=!isShow' >完成</div>
                <div class="del" @click='del'>删除选中({{count}})</div>
            </div>
        </div>
        
        <div class="rack-top" v-else>
            <div class="fl top-title" >让阅读成为习惯</div>
            <div class="fr top-show">
                <svg t="1595506292826" class="icon" viewBox="0 0 1200 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3490" width="18" height="18"><path d="M1100 502q-44-42-104-42t-104 42q-42 44-42 104t42 104q44 42 104 42t104-42q42-44 42-104t-42-104l0 0zM700 502q-44-42-104-42t-104 42q-42 44-42 104t42 104q44 42 104 42t104-42q42-44 42-104t-42-104l0 0zM300 502q-42-42-104-42-60 0-102 42-44 44-44 104t44 104q42 42 102 42 62 0 104-42 42-44 42-104t-42-104l0 0z" p-id="3491"></path></svg>
            </div>
            <div class="fr top-big" @click="toggle({name:'Menu'})">
                <svg t="1595506463549" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4424" width="18" height="18"><path d="M428.8512 857.7024C192.512 857.7024 0 665.1904 0 428.8512 0 192.512 192.512 0 428.8512 0c236.368457 0 428.8512 192.512 428.8512 428.8512 0 95.553829-31.626971 186.426514-88.824686 260.768914l237.860572 237.860572a56.056686 56.056686 0 1 1-78.994286 79.491657l-238.153143-238.094629a426.335086 426.335086 0 0 1-260.739657 88.824686z m0-745.589029A316.123429 316.123429 0 0 0 112.113371 428.8512c0 175.425829 141.312 316.767086 316.737829 316.767086s316.767086-141.312 316.767086-316.767086c0-175.425829-141.312-316.737829-316.767086-316.737829z" fill="#333333" p-id="4425"></path></svg>
            </div>
        </div>
        <div class="box-scroll">
            <div class="rack-bottom">
                <div class="rack-list clearfix">
                    <div class="rack-list-one fl" v-for="(item,index) in rackList" :key="index" @touchstart="setBook()" @touchend="end(item)" ref="tocheck">
                        <div class="list-check" :class="{'active':isShow}" >
                            <van-checkbox v-model="item.isActive" @click='Onecheck'></van-checkbox>
                        </div>
                        <div class="list-img">
                            <img :src="item.cover" alt="" class="auto-img">
                        </div>
                        <div class="list-title one-text">
                            {{item.title}}
                        </div>
                    </div>
                    <div class="rack-list-one fl"  @click="toggle({name:'Menu'})">
                        <div class="list-img">
                            <img src="../../assets/images/addbook.png" alt="" class="auto-img">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
    export default {
        data(){
            return{
                rackList:[],
                isShow:true,
                loop:null,
                allCheck:true,
                count:0
            }
        },
        created(){
            this.getMybook()
            this.get()
        },
        methods:{
            toggle(e){
                this.$router.push(e)
            },
            getMybook(){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "",
                        params: {
                            
                        }
                    }).then(result => {
                        this.$toast.clear(); 
                        
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            
            get(){
                this.rackList = []

                let obj = localStorage.valueOf()
                
                for(let key in obj){
                    if(key.slice(0,8)=='rackList'){
                        let top = JSON.parse(obj[key])
                        top.isActive = false
                        this.rackList.push(top)
                    }
                }

               
            },
            setBook(){

                this.loop = setTimeout(()=>{
                    this.isShow = false
                    for(let i = 0 ; i < this.rackList.length ; i ++){
                        this.rackList[i].isActive = false
                    }
                },500)  
            },
            end(item){
                clearTimeout(this.loop)
                if(this.isShow){
                    this.toggle({name:"Bookmsg",query:{id:item._id,title:item.title}})
                }
                
            },
            //全选
            allcheck(){
                if(this.allCheck){
                    for(let i = 0 ; i < this.rackList.length ; i ++){
                        this.rackList[i].isActive = true
                    }
                    this.allCheck = false
                }else{
                   for(let i = 0 ; i < this.rackList.length ; i ++){
                        this.rackList[i].isActive = false
                    } 
                    this.allCheck = true
                }
                this.num()
            },
            //单选
            Onecheck(){
                for(let i = 0 ; i < this.rackList.length ; i ++){
                    if(!this.rackList[i].isActive){
                        this.allCheck = true
                    }
                }
                this.num()
            },
            //选中数量
            num(){
                this.count = 0
                for(let i = 0 ; i < this.rackList.length ; i ++){
                    if(this.rackList[i].isActive){
                        this.count++
                    }
                }
            },
            //删除书籍
            del(){
                let arr = []
                for(let i = 0 ; i < this.rackList.length ; i ++){
                    if(this.rackList[i].isActive){
                        arr.push(this.rackList[i]._id)
                    }
                }
                
                for(let j = 0 ; j < arr.length ; j ++){
                    localStorage.removeItem('rackList'+arr[j])
                }
                this.get()
            }
        }
    }
</script>

<style lang="less" scoped>
.del{
    position: absolute;
    text-align: center;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    color: red;
    width: 340px;
    left: 15px;
    bottom: 10px;
    background-color: #fff;
    border-radius: 15px;
    border: 1px solid #ccc;
}
.mask{
    position: absolute;
    width: 100%;
    height: 667px;
    background-color: rgba(255, 255, 255, 0.6);
    z-index: 999;
}
.mask.active{
    display: none;
}
.list-check{
    position: absolute;
    left: -10px;
    top: -10px;
    z-index: 1000;
}
.list-check.active{
    display: none;
}
.rack-top{
    z-index: 1000;
    // background-color: #fff;
    padding: 20px;
    .top-title{
        font-size: 20px;
        font-weight: 700;
    }
    .top-show{
       line-height: 30px;
    }
    .top-big{
       margin-right: 35px;
       line-height: 35px;
    }
    
}
.rack-bottom{
    width: 92%;
    margin: 20px  auto 50px;
    border-radius: 10px;
    min-height: 490px;
    background-color: #fff;
}
.rack-list{
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    .rack-list-one{
        position: relative;
        width: 26%;
        margin: 10px 10px;
    }
    .list-img{
        width:100% ;
        border-radius: 10px;
        overflow: hidden;
    }
    .list-title{
        text-indent: 1em;
        margin-top: 5px;
        font-weight: 700;
    }
}
.box-scroll{
    position: absolute;
    left: 0px;
    top: 50px;
    width: 100%;
    height: 600px;
    overflow: hidden;
    overflow-y: scroll;
}
.box-scroll::-webkit-scrollbar {
    display: none;
}
</style>